<template>
  <div class="row">
    <div class="col-12 mt-3" v-if="isReady && userData.avatar">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">My account</h5>
          </div>
          <div class="flex-align-center flex-end">
            <el-button
              @click="cancelEdit"
              type="primary"
              v-ripple.400
              class="flat"
              v-if="isReady && startEditing"
            >
              <i class="bx bx-x mr-1"></i> Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              type="primary"
              v-if="isReady && startEditing"
            >
              <i class="bx bx-save mr-1"></i> Save
            </el-button>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="box">
            <div class="flex-x-between">
              <p class="mt-0 mb-2 text-bold">Details</p>
              <p
                class="hover-underline primary"
                @click="(active = true), (type = 'Password')"
              >
                <i class="bx bx-lock-alt mr-1"></i> Change password
              </p>
            </div>
            <div class="row">
              <div class="col-12 mb-3">
                <label for="" class="custom-label">Avatar</label>
                <file
                  @completed="setImage"
                  :initialData="
                    userData.avatar ? [{ image: userData.avatar }] : []
                  "
                />
              </div>
              <div class="col-6">
                <label for="name" class="custom-label">First Name</label>
                <el-input
                  name="name "
                  class="w-100 mb-3"
                  v-model="userData.first_name"
                />
              </div>
              <div class="col-6">
                <label for="name" class="custom-label">Last Name</label>
                <el-input
                  name="name "
                  class="w-100 mb-3"
                  v-model="userData.last_name"
                />
              </div>
              <div class="col-12">
                <label for="email" class="custom-label">Email Address</label>
                <el-input
                  name="email "
                  class="w-100 mb-3 disabled"
                  v-model="userData.email"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="active"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Changing {{ type }}</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>

      <div class="con-content">
        <div class="row">
          <div class="col-12 mb-2">
            <div class="w-100" v-if="type === 'Email'">
              <label for="email" class="custom-label">Email Address</label>
              <el-input
                name="email "
                class="w-100 mb-3"
                v-model="userData.email"
              />
            </div>
            <div class="w-100" v-if="type === 'Password'">
              <label for="Old Password" class="custom-label"
                >Old Password</label
              >
              <el-input
                name="Old Password "
                class="w-100 mb-3"
                v-model="old_password"
                label="Old Password"
                type="password"
              />

              <label for="New Password" class="custom-label"
                >New Password</label
              >
              <el-input
                name="New Password "
                class="w-100 mb-3"
                v-model="password"
                type="password"
              />

              <label for="New Password Confirmation" class="custom-label"
                >New Password Confirmation</label
              >
              <el-input
                name="New Password Confirmation "
                class="w-100 mb-3"
                v-model="password_confirm"
                label="New Password Confirmation"
                type="password"
              />
            </div>
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="active = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="confirming"
            @click="confirm"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { url } from "../../../core/constant/data";
import { file } from "../../common";
export default {
  data() {
    return {
      url: url,
      active: false,
      type: "Email",
      userData: {
        avatar: "",
      },
      saving: false,
      startEditing: false,
      confirming: false,
      old_password: "",
      password: "",
      password_confirm: "",
    };
  },
  components: {
    file,
  },
  watch: {
    user(Value) {
      if (Value) {
        this.setUser();
        this.$store.commit("setIsReady", true);
      }
    },
    userData: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
  },
  mounted() {
    if (this.user) {
      this.setUser();
      this.$store.commit("setIsReady", true);
    }
  },
  computed: {
    ...mapState(["user", "store", "isReady"]),
    ...mapGetters(["checkIsReady"]),
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    setUser() {
      this.userBeforeEdit = JSON.stringify(this.user);
      this.userData = this.user;
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    setImage($event) {
      if ($event.length > 0) {
        this.userData.avatar = $event[0].image;
      } else {
        this.userData.avatar = "/media/default.png";
      }
    },
    cancelEdit() {
      this.userData = JSON.parse(this.userBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    Post() {
      this.saving = true;
      this.userData.username =
        this.userData.first_name.replace(/\s+/g, "_").toLowerCase() +
        "_" +
        this.userData.last_name.replace(/\s+/g, "_").toLowerCase();
      this.$axios
        .put("auth/", { data: this.userData })
        .then((response) => {
          this.$store.commit("setUser", response.data);
          this.userBeforeEdit = JSON.stringify(response.data);

          this.saving = false;
          this.$noti.display({
            color: "success",
            title: "Success",
            message: "Cool! You profile has been successfully updated.",
          });
          setTimeout(() => {
            this.startEditing = false;
          }, 100);
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    confirm() {
      this.confirming = true;
      if (this.type === "Email") {
        this.$axios
          .post("change-email/", { email: this.userData.email })
          .then(() => {
            this.confirming = false;
            this.active = false;
            this.$noti.display({
              color: "success",
              title: "Success",
              message:
                "The email comfimation message is sent to your email, please check your email!",
            });
          })
          .catch((err) => {
            this.confirming = false;
            this.active = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }

      if (this.type === "Password") {
        this.$axios
          .put("change-password/", {
            old_password: this.old_password,
            password: this.password,
            password_confirm: this.password_confirm,
          })
          .then(() => {
            this.confirming = false;
            this.active = false;
            this.$noti.display({
              color: "success",
              title: "Success",
              message: "Changing password success!",
            });
          })
          .catch((err) => {
            this.confirming = false;
            this.active = false;
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
          });
      }
    },
  },
};
</script>

